import React from "react";
import { graphql } from "gatsby";

import Wrap from "../../components/layout/bludit/wrap";
import Page from "../../components/layout/bludit/page";
import Card from "../../components/basic/bludit/card";

const Bludit = ({ data }) => (
  <Wrap>
    <Page
      title="Latest Posts from "
      url="https://pilab.dev"
      linkTitle="Pi Lab"
      alt="Pi Lab"
    >
      {data.allBludit.edges.map(({ node }) => {
        return (
            <Card
                title={node.title}
                image={node.coverImage}
                url={node.permalink}
                tags={node.tags}
            />
        );
      })}
    </Page>
  </Wrap>
);

export const query = graphql`
  {
    allBludit(limit: 8) {
      edges {
        node {
          id
          title
          coverImage
          permalink
          tags
          slug
        }
      }
    }
  }
`;

export default Bludit;
